import React,{useState,useEffect} from 'react';
import MUIRichTextEditor from 'mui-rte';
import Navbar from '../Navbar/navbar';
import {Container,Box,makeStyles,Typography,TextField,withStyles,
IconButton,InputAdornment,createMuiTheme, MuiThemeProvider,
ListItem,List,Chip} from '@material-ui/core';
import CheckIcon from '@material-ui/icons/Check';
import {converToRaw} from 'draft-js';
import WrittenBlogUpload from '../../firebase/firebaseWrittenBlog';
import Loader from '../../Reusable/loader/loader';

const CssTextField = withStyles({
	  root: {
	    '& label.Mui-focused': {
	      color: 'green',
	    },
	    '& .MuiInput-underline:after': {
	      borderBottomColor: 'green',
	    },
	    '& .MuiOutlinedInput-root': {
	      '& fieldset': {
	        borderColor: 'lightgreen',
	      },
	      '&:hover fieldset': {
	        borderColor: 'yellow',
	      },
	      '&.Mui-focused fieldset': {
	        borderColor: 'green',
	      },
	    },
	  },
})(TextField);

const defaultTheme = createMuiTheme()
 
Object.assign(defaultTheme, {
    overrides: {
        MUIRichTextEditor: {
            root: {
                marginTop: 10,
                width: "100%"
            },
            editor: {
            	borderBottom:'2px solid #00e600',
            }
        }
    }
})

const styles = makeStyles((theme)=>({
	categorybox:{
		width:'30%',
		float:'left',
		marginLeft:'0%',
		paddingLeft:'0%',
		marginTop:'2%',
		marginBottom:'2%',
		['@media (max-width:600px)']:{
			width:'100%',
			marginTop:'5%',
		}
	},
	editor:{
		width:'60%',
		float:'right',
		margin:'1%',
		['@media (max-width:600px)']:{
			width:'100%',
			marginTop:'5%',
			float:'left',
			margin:'0%',
		}
	},
	chip:{
	    color:'white',
	    fontWeight:'bold',
	    backgroundImage:'linear-gradient(to right, #00e600, #00e600)'
	 }
}));
export default function MyEditor(argument) {
	// body...
	const classes = styles();
	const [data,setData] = useState('');
	const [save,setSave] = useState(false);
	const [loading,setLoading] = useState(false);
	const [category,setCategory] = useState();
	const [title,setTitle] = useState();
    const handleSave=(data)=>{
    	setData(data);
    	setSave(true);
    	setLoading(true);
    }
	return(
		<div>
		<Navbar/>
		<Box className={classes.categorybox}>
		<Typography variant="body2" gutterBottom>Add Category to Blog(<small> it helps reader's a lot.</small>)</Typography>
		<CssTextField
           type="text"
           variant="outlined"
           label="Category"
           value={category}
           onChange={(event)=>setCategory(event.target.value)}
           style={{marginTop:'1%'}}
           InputProps={{
            startAdornment:(
            <InputAdornment>
              <IconButton>
               <CheckIcon  style={{color:"#00e600",verticalAlign:'middle'}} />
              </IconButton>
            </InputAdornment>
            )
           }}/>
        <Typography variant="body2" style={{marginTop:"1%"}} gutterBottom>Select from below or write you own</Typography>
        <List style={{marginLeft:"1%"}}>
	      <ListItem>
	        <Chip
	        className={classes.chip}
	        label="How to"
	        onClick={()=>setCategory("How to")}
	        />
	      </ListItem>
	      <ListItem>
	        <Chip
	        className={classes.chip}
	        label="Tips for"
	        onClick={()=>setCategory("Tips for")}
	        />
	      </ListItem>
	      <ListItem>
	        <Chip
	        className={classes.chip}
	        label="Solutions"
	        onClick={()=>setCategory("Solutions")}
	        />
	      </ListItem>
	    </List>
        <Typography variant="body2" gutterBottom>Choose a title of your blog</Typography>
		<CssTextField
           type="text"
           variant="outlined"
           label="Title"
           value={title}
           onChange={(event)=>setTitle(event.target.value)}
           style={{marginTop:'1%'}}
           InputProps={{
            startAdornment:(
            <InputAdornment>
              <IconButton>
               <CheckIcon  style={{color:"#00e600",verticalAlign:'middle'}} />
              </IconButton>
            </InputAdornment>
            )
           }}/>
		</Box>
		<Container className={classes.editor}>
		<MuiThemeProvider theme={defaultTheme}>
		<MUIRichTextEditor 
		inlineToolbar={true}
		className={classes.editor}
		label="start writing here..."
		onSave={handleSave}/>
		</MuiThemeProvider>
		</Container>
		{
			save ? <WrittenBlogUpload setSave={setSave} wblog={data} blogTitle={title} blogCategory={category} setLoading={setLoading}/> : null
		}
		{
			loading ? <Loader/> :null
		}
		</div>)
}