import React,{useState,useEffect} from "react"; 
import Firebase from 'firebase';
import {auth,firestore} from './firebaseInit';
import Error from '../Reusable/error/error';
import Toast from '../Reusable/toast/toast';
import {nanoid} from '@reduxjs/toolkit';

const WrittenBlogUpload = props =>{
  const [openDialog,setOpenDialog] = useState(false);
  const [error,setError] = useState();
  const [toast,setToast] = useState(false);
  const [toastMessage,setToastMessage] = useState();

	useEffect(()=>{
	Firebase.auth().onAuthStateChanged((user)=>{
		if (user) {
			const wBlog = {
			uid:user.uid,
			blogDetails:[
			{  
				blogId:nanoid().slice(0,4),
				blogTitle:props.blogTitle,
				blogCategory:props.blogCategory,
				author:user.displayName,
				uid:user.uid,
				photoURL:user.photoURL,
				wblog:[props.wblog],
			    dateC:Firebase.firestore.Timestamp.fromDate(new Date())
			}
			]
		}
		const db = Firebase.firestore();

		db.collection("written-blogs").doc(user.uid).get()
		.then((doc)=>{
			if(doc.exists){
				db.collection("written-blogs").doc(user.uid).update({
				blogDetails:Firebase.firestore.FieldValue.arrayUnion(
					{
						blogId:nanoid().slice(0,4),
						blogTitle:props.blogTitle,
						blogCategory:props.blogCategory,
						author:user.displayName,
						wblog:props.wblog,
					    dateC:Firebase.firestore.Timestamp.fromDate(new Date())
					})
			})}
			else{
				db.collection("written-blogs").doc(user.uid).set(wBlog)
			}
			props.setLoading(false);
			setToastMessage("Saved !");
			setToast(true);
			window.setTimeout(()=>{props.setSave(false)},3000);
			})
		.catch((error)=>{
			props.setLoading(false);
			setError(error.message);
			setOpenDialog(true);
		})
		}
		else{
			alert("No user Logged in.")
		}
	})
	},[])
	if (toast || openDialog) {
		return(<div>
		 {
		 	openDialog ? <Error error={error}/> : null
		 }
		 {
		 	toast ? <Toast toast={toast} setToast={setToast} message={toastMessage}/> : null
		 }
		</div>)
	}
	else{
		return(<div></div>)
	}
}

export default WrittenBlogUpload;